<template>
  <div>
    <h1>채팅 이력</h1>
    <ul>
      <li v-for="chat in chatrooms" :key="chat.id">{{ chat.name }}</li>
    </ul>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      chatrooms: [],
    };
  },
  async mounted() {
    console.log("chatlist mount");
    try {
      // localStorage에서 id 가져오기
      const userId = localStorage.getItem("id");
      console.log(userId);
      // id를 포함한 API 호출
      const response = await axios.post(`/api/chat/list`, {
        id: userId,
      });

      // 가져온 데이터를 chats 배열에 저장
      this.chatrooms = response.data;
    } catch (error) {
      console.error("Error fetching chat list:", error);
    }
  },
};
</script>
